import common from '../../common/common.js';
import utils from '../../common/util.js';
export default {
	data() {
		return {
			userObj: {},
			tableList: [{
					id: 1,
					nailArtistId: 1,
					startTimeDesc: '2019-09-01',
					endTimeDesc: "2019-09-01",
					nailArtist: {}
				},
				{
					id: 2,
					nailArtistId: 1,
					startTimeDesc: '2019-09-01',
					endTimeDesc: "2019-09-01",
					nailArtist: {}
				}
			]
		};
	},
	onReady() {
		let userObj = uni.getStorageSync('userObj');
		this.userObj = userObj
	},
	onShow() {
		this.getTableList()
	},
	mounted() {
		this.getTableList()
	},
	methods: {
		getTableList: function() {
			uni.request({
				url: common.webUrl + 'appointment/getAllList',
				data: {
					search: {
						userId: this.userObj.id
					}
				},
				method: 'GET',
				dataType: '',
				success: (res) => {
					if (res.data.resCode == '0000') {
						this.tableList = res.data.resultList
					}
				}
			});
		},
		cancleAppoint: function(item) {
			item.state = 2
			uni.request({
				url: common.webUrl + 'appointment/changeState',
				data: item,
				method: 'POST',
				dataType: 'json',
				success: (res) => {
					if (res.data.resCode == '0000') {
						uni.showToast({
							title: "取消成功"
						})
					}
				}
			});
		},

	}
}
